import ImageProfile from "@/images/img_profile.webp";
import Filecertificate from "@/components/icons/filecertificate";
import Filedownload from "@/components/icons/filedownload";

export default function AboutMe() {
    const imageAltName = "Ernesto_De_La_Cruz_Campos";

    return (
        <>
            <article className="bg-red-600 max-w-[600px] flex flex-col items-center justify-center gap-4 text-gray-700 dark:text-gray-200 rounded-lg p-4">
                <div
                    className="grid grid-cols-3 grid-rows-3 gap-4 flex items-center justify-center"
                >

                    <div className="row-span-2">
                        <img
                            src={ImageProfile.src}
                            alt={imageAltName}
                            className="rounded-lg max-w-[100px] max-h-[150px] aspect-[2/3]"
                        />
                    </div>

                    <div className="col-span-2 row-span-2">
                        <div className="w-full flex flex-col items-start justify-start gap-2">
                            <p className="text-lg font-bold text-gray-200">
                                Saludos, soy
                            </p>
                            <p className="text-2xl font-bold text-orange-300">
                                Ernesto De La Cruz Campos
                            </p>
                        </div>

                        <div className="text-base flex flex-col gap-2">
                            <div className="flex items-center gap-2">
                                <Filecertificate size={24} color="currentColor" />
                                <span className="">Ingeniero en Sistemas Computacionales</span>
                            </div>

                            <div className="flex items-center gap-2">

                                <Filedownload size={24} color="currentColor" />
                                <span className="">Instituto Tecnológico Superior de Villa La Venta</span>
                            </div>
                        </div>
                    </div>

                    <div className="col-span-3 row-start-3">
                        <span className="text-lg text-gray-200">Soy un recién egresado de la carrera en busca de oportunidad laboral en el área. Mis principales habilidades se basan en la resolución de problemas mediante análisis lógico y la curiosidad de explorar los límites técnicos de las herramientas.</span>
                    </div>

                </div>
            </article>
        </>
    );
}